import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ToolsService {

  constructor() { }

  public tools: Tool[] = [
    new Tool({
      name: '加密/解密',
      url: '/pages/tools/encrypt-decrypt'
    }),
    new Tool({
      name: '格式化',
      url: '/pages/tools/format'
    }),
    new Tool({
      name: 'Markdown/HTML',
      url: '/pages/tools/markdown-html'
    }),
    new Tool({
      name: 'Placeholder',
    }),
    new Tool({
      name: 'Placeholder2',
    }),
  ];
}

class Tool {
  constructor({name, icon, description, url}: {
    name: string
    icon?: string
    description?: string
    url?: string
  }) {
    this.name = name;
    this.icon = icon;
    this.description = description;
    this.url = url;
  }

  name: string
  icon: string
  description: string
  url: string
}
